import React from 'react'
import styled from 'styled-components'
import ImageUploader from 'react-images-upload';

import theme from '@style/theme'
import { Button, H3, Form, Grid2_3 } from '@style/global'
import Collapse from './components/collapse';
import Calendar from './components/calendar';
import Poster from './components/poster';


class Info extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      title: null,
      subTitle: null,
      speakerName: null,
      speakerInfo: null,
      hostName: null,
      hostInfo: null,
      commentatorName: null,
      commentatorInfo: null,
      abtTalk: null,
      abtSpeaker: null,
      date: null,
      time: null,
      place: null,
      organizer: null,
      coOrganizer: null,
      url: null,
      pictures: [],
    };
    this.handleChange = this.handleChange.bind(this);
    this.onDrop = this.onDrop.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleCallback = (childData) =>{
    this.setState({
      date: childData.selectedDay,
      time: childData.time,
      pictures: childData.pictures,
    });
    console.log("Calendar data is called:" + this.state.date + this.state.time)
  }

  handleChange(event) {
    const target = event.target;
    const value = target.value;
    const name = target.name;
    
    this.setState(
      {
        [name]: value
      }
    );
    console.log(this.state)
  }

  onDrop(pictureFiles, pictureDataURLs) {
      this.setState({
        pictures: this.state.pictures.concat(pictureFiles)
      });
  }

  handleSubmit(event) {
    this.props.parentCallback(this.state);
    event.preventDefault();
  }

  render() {
    return (
      <>
        <H3><b>Poster Creator</b><br/><small>College of Social Sciences, NTU</small></H3>
        <Form onSubmit={this.handleSubmit}>
          <InputBox>
            <Collapse title="Topic" key="Topic" open='true'>
              <InputItem>
                <label htmlFor="title">Title</label>
                <input id="title" type="text" name="title" value={this.state.title} onChange={this.handleChange}/>
              </InputItem>
              <InputItem>
                <label htmlFor="subTitle">Sub-title</label>  
                <input id="subTitle" type="text" name="subTitle" value={this.state.subtitle} onChange={this.handleChange}/>
              </InputItem>
            </Collapse>
            <Collapse title="Speaker & Host" key="Speaker & Host">
              <InputItem>
                <label htmlFor="speaker">Speaker</label>
                <Grid2_3>
                <input type="text" name="speakerName" value={this.state.speakerName} onChange={this.handleChange} placeholder="Name"/>
                <input type="text" name="speakerInfo" value={this.state.speakerInfo} onChange={this.handleChange} placeholder="Info"/>
                </Grid2_3>
              </InputItem>
              <InputItem>
                <label htmlFor="host">Host</label>
                <Grid2_3>
                <input type="text" name="hostName" value={this.state.hostName} onChange={this.handleChange} placeholder="Name"/>
                <input type="text" name="hostInfo" value={this.state.hostInfo} onChange={this.handleChange} aria-label="Host Info" placeholder="Info"/>
                </Grid2_3>
              </InputItem>
            </Collapse>
            <Collapse title="commentator" key="Commentator">
              <InputItem>
                <label htmlFor="Commentator">Commentator</label>
                <Grid2_3>
                  <input type="text" name="commentatorName" value={this.state.commentatorName} onChange={this.handleChange} placeholder="Name"/>
                  <input type="text" name="commentatorInfo" value={this.state.commentatorInfo} onChange={this.handleChange} aria-label="Commentator Info" placeholder="Info"/>
                </Grid2_3>
              </InputItem>
            </Collapse>
            <Collapse title="About" key="About">
              <InputItem>
                <label htmlFor="abtTalk">About The Talk</label>
                <input type="text" name="abtTalk" value={this.state.abtTalk} onChange={this.handleChange}/>
              </InputItem>
              <InputItem>
                <label htmlFor="abtSpeaker">About The Speaker</label>
                <input type="text" name="abtSpeaker" value={this.state.abtSpeaker} onChange={this.handleChange}/>
              </InputItem>
            </Collapse>
            <Collapse title="Time & Place" key="Time & Place" overflowWhenOpen="visible">
              <InputItem>
                <label htmlFor="time">Time</label>
                <Grid2_3>  
                  <Calendar parentCallback = {this.handleCallback}/>
                  <InputItem>
                    <label htmlFor="place">Place</label>
                    <select name="place" onBlur={this.handleChange} value={this.state.place} placeholder="Please select a place">
                      <option value="Room 401">Room 401</option>
                      <option value="Room 101">Room 101</option>
                      <option value="Room 319">Room 319</option>
                      <option value="Room 318B">Room 318B</option>
                    </select>
                  </InputItem>
                </Grid2_3>
              </InputItem>
            </Collapse>
            <Collapse title="Organizer" key="Organizer">
              <InputItem>
                <label htmlFor="Org">Main Organizer</label>
                <input type="text" name="organizer" value={this.state.organizer} onChange={this.handleChange}/>
              </InputItem>
              <InputItem>
                <label htmlFor="coOrg">Co-organizer</label>
                <input type="text" name="coOrganizer" value={this.state.coOrganizer} onChange={this.handleChange}/>
              </InputItem>
            </Collapse>
            <Collapse title="Image Upload & QR Code" key="Other">
              <InputItem>
                <label htmlFor="qr">QR Code</label>
                <input type="url" name="url" value={this.state.url} onChange={this.handleChange} placeholder="Please enter a URL"/>
              </InputItem>
              <InputItem>
                <label htmlFor="book">Book Cover</label>
                <ImageUploader
                  withIcon={false}
                  withPreview={true}
                  label=""
                  buttonText="Upload Images"
                  onChange={this.onDrop}
                  imgExtension={[".jpg", ".gif", ".png", ".gif", ".svg"]}
                  maxFileSize={1048576}
                  fileSizeError=" file size is too big"
                  parentCallback = {this.handleCallback}
                />
              </InputItem>
            </Collapse>
          </InputBox>
          <Button Tertiary>
            <input type="submit" value="Preview"/>
          </Button>
          <Button Tertiary>
            <input type="button" value="Print"/>
          </Button>
        </Form>
      </>
    );
  }
}


class Generator extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      title: null,
      subTitle: null,
      speakerName: null,
      speakerInfo: null,
      hostName: null,
      hostInfo: null,
      commentatorName: null,
      commentatorInfo: null,
      abtTalk: null,
      abtSpeaker: null,
      date: null,
      time: null,
      place: null,
      organizer: null,
      coOrganizer: null,
      url: null,
      pictures: null,
    };
  }

  handleCallback = (childData) =>{
    this.setState({
      title: childData.title, 
      subTitle: childData.subTitle,
      speakerName: childData.speakerName,
      speakerInfo: childData.speakerInfo,
      hostName: childData.hostName,
      hostInfo: childData.hostInfo,
      commentatorName: childData.commentatorName,
      commentatorInfo: childData.commentatorInfo,
      abtTalk: childData.abtTalk,
      abtSpeaker: childData.abtSpeaker,
      date: childData.date,
      time: childData.time,
      place: childData.place,
      organizer: childData.organizer,
      coOrganizer: childData.coOrganizer,
      url: childData.url,
      pictures: childData.file,
    })
  }

  render(){
    return (
      <Grid>
        <Input>
          <Info parentCallback = {this.handleCallback}/>
        </Input>
        <Output>
          <LifeView data={this.state}/>
        </Output>
      </Grid>
    )
  }
}


const LifeView = ({data}) =>  (
  <LifeViewBox>
    <Poster 
      title={data.title} 
      subTitle={data.subTitle}
      speakerName= {data.speakerName}
      speakerInfo= {data.speakerInfo}
      hostName= {data.hostName}
      hostInfo= {data.hostInfo}
      commentatorName= {data.commentatorName}
      commentatorInfo= {data.commentatorInfo}
      abtTalk= {data.abtTalk}
      abtSpeaker= {data.abtSpeaker}
      date= {data.date}
      time= {data.time}
      place= {data.place}
      organizer= {data.organizer}
      coOrganizer= {data.coOrganizer}
      url= {data.url}
      pictures= {data.file}
    />
  </LifeViewBox>  
);



export default Generator

